import { setPageNumber } from './paging';
import * as Utilities from '../forum/utilities';
export function getPaginationData(pageSize, pageNumber, isPageChange) {
    const placeHolder = document.getElementById('PostAttachmentListPlaceholder');
    const list = document.querySelector('.AttachmentList');
    const yafUserId = placeHolder.dataset.userid || '0';
    const pagedResults = {
        UserId: parseInt(yafUserId),
        PageSize: pageSize,
        PageNumber: pageNumber
    };
    const ajaxUrl = '/api/Attachment/GetAttachments';
    fetch(ajaxUrl, {
        method: 'POST',
        body: JSON.stringify(pagedResults),
        headers: {
            Accept: 'application/json',
            "Content-Type": 'application/json;charset=utf-8',
            "RequestVerificationToken": document.querySelector('input[name="__RequestVerificationToken"]').value
        }
    }).then(res => res.json()).then(data => {
        list.innerHTML = '';
        document.getElementById('PostAttachmentLoader').style.display = 'none';
        if (data.attachmentList.length === 0) {
            const li = document.createElement('div');
            li.classList.add('col');
            const noText = placeHolder.dataset.notext;
            const noAttachmentsText = noText || '';
            li.innerHTML = `<div class="alert alert-info text-break" role="alert" style="white-space:normal;width:200px">${noAttachmentsText}</div>`;
            list.appendChild(li);
        }
        let count = 0;
        data.attachmentList.forEach((dataItem) => {
            const li = document.createElement('div');
            li.classList.add('col-6', 'col-sm-4');
            li.style.cursor = 'pointer';
            li.setAttribute('onclick', dataItem.onClick);
            li.innerHTML = dataItem.iconImage;
            list.appendChild(li);
            count++;
        });
        Utilities.renderAttachPreview('.attachments-preview');
        setPageNumber(pageSize, pageNumber, data.totalRecords, document.getElementById('AttachmentsListPager'), 'Attachments', 'getPaginationData');
    }).catch((error) => {
        console.error(error);
        document.getElementById('PostAttachmentLoader').style.display = 'none';
        placeHolder.textContent = error.toString();
    });
}
const _global = (window /* browser */ || global /* node */);
_global.getPaginationData = getPaginationData;
